import React from 'react';
import p5 from 'p5'

class Sketch extends React.Component {
  constructor(props) {
    super(props)
    this.myRef = React.createRef()
  }

  Sketch = (p) => {
    var angle = 0;
    var slider;
  

    p.setup = () => {
      p.createCanvas(600, 400);
      p.noLoop();
      // slider = p.createSlider(0, p.TWO_PI, p.PI / 4, 0.01);
    }
    
    p.draw = () => {
      p.background(255);
      // angle = p.TWO_PI * p.random(0, 0.01);
      angle = p.TWO_PI * 0.06;
      console.log('angle: ', angle)
      p.stroke(0);
      p.translate(250, p.height);
      p.branch(100);
    }
    
    p.branch = (len) => {
      p.line(0, 0, 0, -len);
      p.translate(0, -len);
      if (len > 4) {
        p.push();
        p.rotate(angle);
        p.branch(len * 0.67);
        p.pop();
        p.push();
        p.rotate(-angle);
        p.branch(len * 0.67);
        p.pop();
      }
    }
  }



  componentDidMount() {
    this.myP5 = new p5(this.Sketch, this.myRef.current)
  }

  render() {
    return (
      <div ref={this.myRef} />
      // <div/>
    )
  }
}

export default Sketch;